.profile-header {
  border: 1px solid
}


.followers-btn,
.following-btn,
.requests-btn {
border: none;
position: relative;
/* border-bottom: 2px solid #faed26; */
width: 100%;
height: 100%;
background: transparent;
padding: 0px;
outline: none;
stroke-linecap: round;
}

.followers-btn:focus,
.following-btn:focus,
.requests-btn:focus {
outline: none;
background: transparent;
}

.follower-row {
/* border-left: 1px solid lightgray;
border-right: 1px solid lightgray; */
display: flex;
flex-wrap: wrap;
height: 100px;
align-items: center;
text-align: center;
position: relative;
border-bottom: 1px solid lightgray;
}

