.playlist-nav-container .rhap_container {
    box-sizing: border-box;
    /* display: flex; */
    /* flex-direction: column; */
    line-height: 1;
    font-family: inherit;
    width: 100%;
    padding: 0px;
    background-color: transparent;
    box-shadow: none;
  }
  .playlist-nav-container .rhap_container:focus:not(:focus-visible) {
    outline: 0;
  }
  .playlist-nav-container .rhap_container svg {
    vertical-align: initial;
  }
  
  .playlist-nav-container .rhap_header {
    margin-bottom: 10px;
  }
  
  .playlist-nav-container .rhap_footer {
    margin-top: 5px;
  }
  
  .playlist-nav-container .rhap_main {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
  }
  
  .playlist-nav-container .rhap_horizontal-reverse {
    flex-direction: row-reverse;
  }
  .playlist-nav-container .rhap_horizontal-reverse .rhap_controls-section {
    margin-right: 8px;
  }
  
  .playlist-nav-container .rhap_progress-section {
    display: flex;
    flex: 3 1 auto;
    align-items: center;
  }
  
  .playlist-nav-container .rhap_progress-container {
    display: flex;
    align-items: center;
    height: 20px;
    flex: 1 0 auto; 
    align-self: center;
    margin: 0 calc(10px + 1%);
    cursor: pointer;
    -webkit-user-select: none;
  }
  .playlist-nav-container .rhap_progress-container:focus:not(:focus-visible) {
    outline: 0;
  }
  .playlist-nav-container 
  .rhap_time {
    color: #333;
    font-size: 16px;
    user-select: none;
    -webkit-user-select: none;
  }
  
  .playlist-nav-container .rhap_progress-bar {
    box-sizing: border-box;
    position: relative;
    z-index: 0;
    width: 100%;
    height: 5px;
    background-color: #dddddd;
    border-radius: 2px;
  }
  
  .playlist-nav-container .rhap_progress-filled {
    height: 100%;
    position: absolute;
    z-index: 2;
    background-color: #868686;
    border-radius: 2px;
  }
  
  .playlist-nav-container  .rhap_progress-bar-show-download {
    height: 1px;    
    background-color: rgba(221, 221, 221, 0.5);
  }
  
  .playlist-nav-container .rhap_download-progress {
    height: 100%;
    position: absolute;
    z-index: 1;
    background-color: black;
    border-radius: 2px;
  }
  
  .playlist-nav-container .rhap_progress-indicator {
    box-sizing: border-box;
    position: absolute;
    z-index: 3;
    width: 10px;
    height: 10px;
    margin-left: -10px;
    top: -5px;
    background: white;
    border-radius: 50px;
    box-shadow: rgba(134, 134, 134, 0.5) 0 0 5px;
  }
  
  .playlist-nav-container  .rhap_controls-section {
    display: flex;
    flex: 1 1 auto;
    justify-content: space-between;
    align-items: center;
  }
  
  .playlist-nav-container .rhap_additional-controls {
    display: flex;
    flex: 1 0 auto;
    align-items: center;
  }
  
  .playlist-nav-container  .rhap_repeat-button {
    font-size: 26px;
    width: 26px;
    height: 26px;
    color: #868686;
    margin-right: 6px;
  }
  
  .playlist-nav-container .rhap_main-controls {
    flex: 0 1 auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .playlist-nav-container .rhap_main-controls-button {
    margin: 0 3px;
    color: white;
    font-size: 35px;
    /* width: 35px;
    height: 35px; */
  }
  
  .playlist-nav-container .rhap_play-pause-button {
    font-size: 40px;
    width: 35px;
    height: 35px;
  }
  
  .playlist-nav-container .rhap_rewind-button {
    font-size: 40px;
    width: 20px;
    height: 20px;
  }

  .playlist-nav-container  .rhap_forward-button {
    font-size: 40px;
    width: 20px;
    height: 20px;
    padding: 0;
  }
  .playlist-nav-container  .rhap_skip-button {
    font-size: 40px;
    width: 20px;
    height: 20px;
    padding: 0;
  }
  .playlist-nav-container .rhap_forward-button svg, 
  .playlist-nav-container .rhap_rewind-button svg,
  .playlist-nav-container .rhap_skip-button svg {
    display: block;
  }
  .playlist-nav-container .rhap_volume-controls {
    display: flex;
    flex: 1 0 auto;
    justify-content: flex-end;
    align-items: center;
  }
  
  .playlist-nav-container .rhap_volume-button {
    flex: 0 0 26px;
    font-size: 26px;
    width: 26px;
    height: 26px;
    color: white;
    margin-right: 6px;
  }
  
  .playlist-nav-container  .rhap_volume-container {
    display: flex;
    align-items: center;
    flex: 0 1 100px;
    -webkit-user-select: none;
  }
  
  .playlist-nav-container  .rhap_volume-bar-area {
    display: flex;
    align-items: center;
    width: 100%;
    height: 14px;
    cursor: pointer;
  }
  .playlist-nav-container .rhap_volume-bar-area:focus:not(:focus-visible) {
    outline: 0;
  }
  
  .playlist-nav-container .rhap_volume-bar {
    box-sizing: border-box;
    position: relative;
    width: 100%;
    height: 1px;
    background: black;
    border-radius: 2px;
  }
  
  .playlist-nav-container .rhap_volume-indicator {
    box-sizing: border-box;
    position: absolute;
    width: 10px;
    height: 10px;
    margin-left: -6px;
    left: 0;
    top: -5px;
    background: white;
    /* opacity: 0.9; */
    border-radius: 50px;
    box-shadow: rgba(134, 134, 134, 0.5) 0 0 3px;
    cursor: pointer;
  }
  .playlist-nav-container .rhap_volume-indicator:hover {
    opacity: 0.9;
  }
  
  /* Utils */
  .playlist-nav-container  .rhap_button-clear {
    background-color: transparent;
    border: none;
    padding: 0;
    overflow: hidden;
    cursor: pointer;
  }
  .playlist-nav-container .rhap_button-clear:hover {
    opacity: 0.9;
    transition-duration: 0.2s;
  }
  .playlist-nav-container .rhap_button-clear:active {
    opacity: 0.95;
  }
  .playlist-nav-container .rhap_button-clear:focus:not(:focus-visible) {
    outline: 0;
  }
  
  .playlist-nav-container .rhap_play-pause-button:focus, 
  .playlist-nav-container  .rhap_rewind-button:focus, 
  .playlist-nav-container  .rhap_skip-button:focus,
  .playlist-nav-container .rhap_forward-button:focus,
  .playlist-nav-container .rhap_volume-button:focus,
  .playlist-nav-container .rhap_main-controls-button:focus,
  .playlist-nav-container .rhap_repeat-button:focus {
    background: transparent;
  }