/* Background color */

.login-container {
  box-shadow: 5px 0px 10px lightgray;
  background: transparent;
  height: 550px;
}
/* Sign In Option on Top */
.login-btns {
  height: 45px;
  padding-left: 0px;
  margin-bottom: 0px;
  border-top: 1px solid lightgray;
  border-right: 1px solid lightgray;
  border-left: 1px solid lightgray;
}

.login-row {
  margin-bottom: 0px
}

.login-btns button {
  width: 50%;
  height: 100%;
  background: transparent;
  border: none;
  outline: none;
 
}

/* For Credentials (Email/Password/Username) */
.cred {
  height: 400px;
  background: white;
  border-left: 1px solid;
  border-right: 1px solid;
  border-color: lightgray;
}

.login-col {
  background: #faed26;
  text-align: center;
  height: 45px;
  /* padding: 10px; */
  padding: 0px;
  border-radius: 0px 0px 5px 5px;
  border: 1px solid;
  border-color: lightgray;
}

/* For the input boxes */
.input-group {
  position: relative;
  margin: 40px 0px 20px;
  background: white;
  padding: 0px 10px;
}

.input-group-signup {
  position: relative;
  margin: 35px 0px 20px;
  background: white;
  padding: 0px 10px;
}

.cred .input-group .input {
  background: white;
  font-size: 18px;
  padding: 0px 0px 0px 5px;
  display: block;
  width: 100%;
  border-radius: 5px;
  border: 1px solid;
}

.cred .input-group .input:focus {
  outline: none;
  border: 2px solid;
}

.cred .input-group .label {
  background-color: transparent;
  color: gray;
  font-size: 18px;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  left: 15px;
  top: 10px;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

.cred .input-group .input:focus ~ .label,
.cred .input-group .input:valid ~ .label {
  top: -14px;
  font-size: 14px;
  background: white;
  padding: 0px 10px;
  color: #1e82fc;
}

.cred .input-group .input:focus,
.cred .input-group .input:valid {
  border-color: #1e82fc;
}

.login-col button {
  border: none;
  outline: none;
}

.error-message {
  height: 20px;
  margin-top: 15px;
  text-align: center;
  color: red;
}
/* Change max container sizes */
/*  Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  .login-container {
    max-width: 400px;
  }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .login-container {
    max-width: 400px;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .login-container {
    max-width: 400px;
  }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .login-container {
    max-width: 400px;
  }
}